@mixin ff($name, $path, $weight, $style) {

  @font-face { 
    font-family: $name;
    src: url('#{$path}.woff2') format('woff2'),
        // url('#{$path}.otf') format('opentype'),
        url('#{$path}.ttf') format('truetype'),
        url('#{$path}.woff') format('woff');
    font-weight: #{$weight};
    font-style: #{$style};
  }
}

@mixin img-2x($url) {
    @media only screen and (min--moz-device-pixel-ratio: 2),
    only screen and (-o-min-device-pixel-ratio: 2/1),
    only screen and (-webkit-min-device-pixel-ratio: 2),
    only screen and (min-device-pixel-ratio: 2) {
      background-image: url($url);
    }
  }

@mixin media($media) {

    @if $media == w1600 {
        @media only screen and (min-width: 1600px) { @content; }
    }

    @else if $media == w1366 {
        @media only screen and (min-width: 1367px) { @content; }
    }

    @else if $media == w1280 {
        @media only screen and (min-width: 1280px) { @content; }
    }

    @else if $media == w1024 {
        @media only screen and (min-width: 1024px) { @content; }
    }

    @else if $media == w768 {
        @media only screen and (min-width: 768px) { @content; }
    }

    @else if $media == w500 {
        @media only screen and (min-width: 500px) { @content; }
    }

    @else if $media == w320 {
        @media only screen and (min-width: 320px) { @content; }
    }
}

@mixin mediaDown($media) {

    @if $media == w1366 {
        @media only screen and (min-width: 1366px) { @content; }
    }

    @else if $media == w1280 {
        @media only screen and (min-width: 1280px) { @content; }
    }

    @else if $media == w1024 {
        @media only screen and (max-width: 1023px) { @content; }
    }

    @else if $media == w768 {
        @media only screen and (max-width: 767px) { @content; }
    }

    @else if $media == w500 {
        @media only screen and (min-width: 500px) { @content; }
    }

    @else if $media == w320 {
        @media only screen and (min-width: 320px) { @content; }
    }
}

@mixin placeholder {
    &::-webkit-input-placeholder {
        @content;
    }

    &:-moz-placeholder {
        @content;
    }

    &::-moz-placeholder {
        @content;
    }

    &:-ms-input-placeholder {
        @content;
    }
}